import React, { Component } from "react";
import DesignSVG from "./../../SVG/DesignSVG";
import AdsSVG from "./../../SVG/AdsSVG";
import InboundSVG from "./../../SVG/InboundSVG";
import DesenvolvimentoSVG from "../../SVG/DesenvolvimentoSVG";


let style = {
  formSection:{
    container:{
      paddingTop: "4.375rem"
    },
    title: {
      textAlign:"center",
      fontSize: "1.875",
      fontWeight: "900",
      fontFamily: "Raleway, sans-serif",
      marginBottom: "4rem"
    }
  }
}
class FormOrcamento extends Component {
  
  handleChange = e => {
    this.setState({ [e.type]: e.target.value });
  };

  onSubmit = event => {
    event.preventDefault()
    console.log(this.state)
  }

  render() {
    return (
      <div style={style.formSection.container}>
      <form className="form-default">
        <div className="container">
          <div className="row">
            <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
              <div className="row">
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-nome" className="lbl-title">
                      Seu nome?*
                    </label>
                    <input
                      onChange={this.handleChange}
                      name="name"
                      type="text"
                      className="form-control border"
                      id="txt-nome"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-sobrenome" className="lbl-title">
                      Seu sobrenome?*
                    </label>
                    <input
                      onChange={this.handleChange}
                      name="lasName"
                      type="text"
                      className="form-control border"
                      id="txt-sobrenome"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-email" className="lbl-title">
                      Seu e-mail?*
                    </label>
                    <input
                      onChange={this.handleChange}
                      name="mailContact"
                      type="email"
                      className="form-control border"
                      id="txt-email"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-site" className="lbl-title">
                      Site da empresa?
                    </label>
                    <input
                      onChange={this.handleChange}
                      name="site"
                      type="site"
                      className="form-control border"
                      id="txt-site"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content form-group">
                    <label htmlFor="txt-empresa" className="lbl-title">
                      Nome da empresa?*
                    </label>
                    <input
                      onChange={this.handleChange}
                      name="company"
                      type="text"
                      className="form-control border"
                      id="txt-empresa"
                      required
                    />
                  </div>
                </div>
                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                <input name="Branding" type="checkbox" id="dsgn-gestao-marca" className="styled-checkbox" />
                <label for="dsgn-gestao-marca">Não sei qual serviço necessito</label>
                </div>

                <div className="col-xl-3 col-lg-3 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content border">
                    <div className="icon design">
                      <DesignSVG />
                    </div>
                    <h3>Design</h3>
                    <ul>
                      <li>
                        <input
                          onChange={() => this.setState({services: {...this.state.services, branding: !this.state.services.branding}})}
                          name="Branding"
                          type="checkbox"
                          id="dsgn-gestao-marca"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-gestao-marca">Branding</label>
                      </li>
                      <li>
                        <input
                          onChange={() => this.setState({services: {...this.state.services, site: !this.state.services.site}})}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="dsgn-site"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-site">Site</label>
                      </li>
                      <li>
                        <input
                          onChange={() => this.setState({services: {...this.state.services, site: !this.state.services.site}})}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="dsgn-loja"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-loja">Loja Virtual</label>
                      </li>
                      <li>
                        <input
                          onChange={this.handleChange}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="dsgn-blog"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-blog">Blog Corporativo</label>
                      </li>
                      <li>
                        <input
                          onChange={this.handleChange}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="dsgn-ux"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-ux">UX</label>
                      </li>
                    </ul>
                  </div>
                </div>

                <div className="col-xl-3 col-lg-3 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content border">
                    <div className="icon design">
                      <DesenvolvimentoSVG/>
                    </div>
                    <h3>Desenvolvimento</h3>
                    <ul>
                      <li>
                        <input
                          onChange={() => this.setState({services: {...this.state.services, branding: !this.state.services.branding}})}
                          name="Branding"
                          type="checkbox"
                          id="dsgn-gestao-marca"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-gestao-marca">Branding</label>
                      </li>
                      <li>
                        <input
                          onChange={() => this.setState({services: {...this.state.services, site: !this.state.services.site}})}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="dsgn-site"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-site">Site</label>
                      </li>
                      <li>
                        <input
                          onChange={() => this.setState({services: {...this.state.services, site: !this.state.services.site}})}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="dsgn-loja"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-loja">Loja Virtual</label>
                      </li>
                      <li>
                        <input
                          onChange={this.handleChange}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="dsgn-blog"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-blog">Blog Corporativo</label>
                      </li>
                      <li>
                        <input
                          onChange={this.handleChange}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="dsgn-ux"
                          className="styled-checkbox"
                        />
                        <label htmlFor="dsgn-ux">UX</label>
                      </li>
                    </ul>
                  </div>
                </div>

                <div className="col-xl-3 col-lg-3 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content border">
                    <div className="icon inbound">
                      <InboundSVG />
                    </div>
                    <h3>Inbound</h3>
                    <ul className="oppening-list">
                      <li>
                        <input
                          onChange={this.handleChange}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="inbd-plano"
                          className="styled-checkbox"
                        />
                        <label htmlFor="inbd-plano">Plano de Marketing</label>
                      </li>
                      <li>
                        <input
                          onChange={this.handleChange}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="inbd-seo"
                          className="styled-checkbox"
                        />
                        <label htmlFor="inbd-seo">SEO</label>
                      </li>
                      <li>
                        <input
                          onChange={this.handleChange}
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="inbd-mkt"
                          className="styled-checkbox"
                        />
                        <label htmlFor="inbd-mkt">Marketing de Conteúdo</label>
                      </li>
                      <li>
                        <input
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="inbd-google"
                          className="styled-checkbox"
                        />
                        <label htmlFor="inbd-google">Google Analytics</label>
                      </li>
                      <li>
                        <input
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="inbd-email"
                          className="styled-checkbox"
                        />
                        <label htmlFor="inbd-email">E-mail Marketing</label>
                      </li>
                    </ul>
                  </div>
                </div>

                <div className="col-xl-3 col-lg-3 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content border">
                    <div className="icon ads">
                      <AdsSVG />
                    </div>
                    <h3>Ads</h3>
                    <ul>
                      <li>
                        <input
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="ads-google"
                          className="styled-checkbox"
                        />
                        <label htmlFor="ads-google">Google Adwords</label>
                      </li>
                      <li>
                        <input
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="ads-face"
                          className="styled-checkbox"
                        />
                        <label htmlFor="ads-face">Facebook Ads</label>
                      </li>
                      <li>
                        <input
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="ads-youtube"
                          className="styled-checkbox"
                        />
                        <label htmlFor="ads-youtube">Youtube Ads</label>
                      </li>
                      <li>
                        <input
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="ads-linkedin"
                          className="styled-checkbox"
                        />
                        <label htmlFor="ads-linkedin">LinkedIn Ads</label>
                      </li>
                      <li>
                        <input
                          name="Branding"
                          type="checkbox"
                          value="Branding"
                          id="ads-remarketing"
                          className="styled-checkbox"
                        />
                        <label htmlFor="ads-remarketing">Remarketing</label>
                      </li>
                    </ul>
                  </div>
                </div>

                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                  <div className="wrapper-content">
                    <label htmlFor="txt-necessidade" className="lbl-title">
                      Nos conte mais sobre a sua situação atual!
                    </label>
                    <textarea id="txt-necessidade" className="form-control" />
                  </div>
                </div>
                <div className="wrapper-content">
                  <button className="button btn-send">
                    Solicite o orçamento
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
    );
  }
}

export default FormOrcamento;



